import request from '@/utils/request'
// import { getToken } from '@/utils/storage'
// 封装API
export const getArticles = params => request.get('/interview/query', { params })

export const getArticleDetail = id => request.get('/interview/show', { params: { id } }) // 通过id值将文章渲染到面经详情页

export const updateLike = id => request.post('/interview/opt', { id, optType: 1 })

export const updateCollect = id => request.post('/interview/opt', { id, optType: 2 })

export const getArticlesCollectOrLike = params => request.get('/interview/opt/list', { params })
